@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Regular.woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold.woff2");
  font-weight: 700;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: 700;
  min-width: 345px;
}

.container {
  max-width: 1440px;
  min-width: 345px;
  margin: 0 auto;
  padding: 0 15px;
}

a {
  text-decoration: none;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.vissualy-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background: url(../img/home-page/big-banner.svg), linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}

.header:target .header__menu {
  top: 0;
}

.header:target .icon__menu {
  position: static;
}

.header__wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  margin-bottom: 129px;
}

.header__wrap-menu-logo {
  grid-column-start: -3;
  grid-column-end: -2;
  height: 57px;
}

.header__wrap-icon-menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header__subtitle {
  font-size: 38px;
  line-height: 46px;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 159px;
}

.header__arrow-down {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.header__arrow-down-border {
  width: 42px;
  height: 42px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__arrow-down-border:hover, .header__arrow-down-border:active {
  filter: invert(31%) sepia(100%) saturate(6241%) hue-rotate(331deg) brightness(94%) contrast(99%);
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(245, 245, 250, 0.2);
  margin: 0 -15px;
  padding: 10px 15px;
  grid-column-start: 1;
  grid-column-end: 3;
}

.nav__lang-list {
  display: flex;
}

.nav__lang-list-item {
  padding: 5px 0;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.nav__lang-list-link {
  border-radius: 8px;
  padding: 5px 7px;
}

.nav__lang-list-link--active {
  background-color: #F0145A;
  color: #FFFFFF;
}

.nav__lang-list-link--dark {
  color: #FFFFFF;
}

.nav__lang-list-link--dark:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.nav__lang-list-link--white {
  color: #B3B3BA;
}

.nav__lang-list-link--white:hover {
  background-color: #F5F5FA;
}

.nav__address {
  display: none;
}

.nav__tel {
  line-height: 20px;
  color: #FFFFFF;
}

.nav__facebook {
  height: 24px;
  display: none;
}

.footer {
  font-size: 20px;
}

.header__menu {
  position: fixed;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #707093;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  transition: top 0.2s;
}

.header__menu-list {
  background-color: #FFFFFF;
  width: 100%;
  border-radius: 12px 12px 0px 0px;
  margin: 0 8px;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
}

.header__menu-list-item {
  background-color: #F5F5FA;
  width: 100%;
  border-radius: 12px;
  text-align: center;
  margin-bottom: 15px;
  padding: 7px 0;
}

.header__menu-list-item:last-child {
  margin-bottom: 12px;
}

.header__menu-list-item:active {
  background-color: #E6E6F0;
}

.header__menu-list-link {
  font-size: 24px;
  line-height: 29px;
  color: #24A3FF;
}

.icon__menu {
  position: relative;
  width: 32px;
  height: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.icon__menu:active {
  background-color: #E6E6F0;
  border-radius: 8px;
}

.icon__menu::after, .icon__menu::before {
  content: "";
  background-color: #FFFFFF;
  height: 2px;
  width: 18px;
}

.icon__menu-span {
  background-color: #FFFFFF;
  height: 2px;
  width: 18px;
  margin: 5px 0;
}

.close-icon__menu {
  display: flex;
  justify-content: center;
  position: relative;
  display: flex;
  align-items: center;
  width: 32px;
  height: 32px;
  align-self: flex-end;
  margin: 12px 0 20px;
}

.close-icon__menu::after, .close-icon__menu::before {
  content: "";
  background-color: #EC6F5D;
  height: 2px;
  width: 75%;
  position: absolute;
}

.close-icon__menu::before {
  transform: rotate(45deg);
}

.close-icon__menu::after {
  transform: rotate(-45deg);
}

.close-icon__menu:hover, .close-icon__menu:active {
  background: #E6E6F0;
  border-radius: 8px;
}
