.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgba(245, 245, 250, 0.2);
    margin: 0 -15px;
    padding: 10px 15px;
    grid-column-start: 1;
    grid-column-end: 3;
    &__lang {
        &-list {
            display: flex;
            &-item {
                padding: 5px 0;
                font-size: 12px;
                line-height: 15px;
                letter-spacing: 1px;
                text-transform: uppercase;
            }
            &-link {
                border-radius: 8px;
                padding: 5px 7px;
                &--active {
                    background-color: #F0145A;
                    color: #FFFFFF;
                    }
                &--dark {
                    color: #FFFFFF;
                }
                &--dark:hover {
                    background-color: rgba(255, 255, 255, 0.2);
                }
                &--white {
                    color: #B3B3BA;
                }
                &--white:hover {
                    background-color: #F5F5FA;
                }
            }
        }
    }
    &__address {
        display: none;
    }
    &__tel {
        line-height: 20px;
        color: #FFFFFF;
    }
    &__facebook {
        height: 24px;
        display: none;
    }
}